
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.filterbarpop-wrap {
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  max-height: 100%;
}
.filterbarpop-wrap .filterbarpop-bg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    max-height: 100%;
    background: rgba(0, 0, 0, 0.6);
}
.filterbarpop-wrap .filterbarpop {
    position: absolute;
    width: 100%;
}
.filterbarpop-wrap .filterbarpop .nav-bar {
      width: 100%;
      display: flex;
      display: -ms-flexbox;
      display: -webkit-box;
      display: -webkit-flex;
      flex-directives: row;
      align-items: center;
      -webkit-align-items: center;
      -webkit-box-align: center;
      -moz-box-align: center;
      -ms-flex-align: center;
      height: 40px;
}
.filterbarpop-wrap .filterbarpop .nav-bar a {
        background: #fff;
        height: 100%;
        line-height: 40px;
        text-decoration: none;
        color: #323232;
        text-align: center;
}
.filterbarpop-wrap .filterbarpop .main {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      display: -webkit-flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      height: 250px;
      background: #fff;
      border-top: 1px solid #f5f5f5;
}
.filterbarpop-wrap .filterbarpop .main .main-sidebar {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 50%;
                flex: 0 0 50%;
        overflow: auto;
        width: 100%;
}
.filterbarpop-wrap .filterbarpop .main .main-sidebar div {
          background: #f5f5f5;
}
.filterbarpop-wrap .filterbarpop .main .full-line {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 100%;
                flex: 0 0 100%;
}
.filterbarpop-wrap .filterbarpop .main .full-line div {
          text-align: left;
          text-indent: 1.5em;
}
.filterbarpop-wrap .filterbarpop .main .main-list {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 50%;
                flex: 0 0 50%;
        overflow: auto;
}
.filterbarpop-wrap .filterbarpop .main .main-list span:active {
          background: #f5f5f5;
}
.filterbarpop-wrap .filterbarpop .main .item {
        display: inline-block;
        height: 40px;
        background: #fff;
        line-height: 40px;
        width: 100%;
        text-decoration: none;
        color: #444;
        text-align: center;
}
.filterbarpop-wrap .filterbarpop .main .item:active {
          color: #fff;
}
.filterbarpop-wrap .is-selected {
    border-bottom: 2px solid orange;
    box-sizing: border-box;
}
.filterbarpop-wrap .s-selected {
    background: #fff !important;
}



