
.filterbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
}
.container {
  width: 100%;
  outline: 1px solid #DBDCDE;
  position: relative;
}
.row {
  display: flex;
  display: -ms-flexbox;
  display: -webkit-box;
  display: -webkit-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  justify-content: space-between;
  -webkit-box-pack: space-between;
  -moz-box-pack: space-between;
  -ms-flex-pack: space-between;
  width: 90%;
  height: 40px;
  margin: 0 auto;
  line-height: 40px;
}
.row .is-selected {
    color: orange;
}



